import React, { useState } from 'react';
import '../../App.css';
import {Link} from 'react-router-dom';
import './ContactUs.css';



const url ="https://telangapadaatmnodeapp.herokuapp.com/users";
const ContactUs = () => {
    const [userRegistration,setuserRegistration]=useState({
     name:"",
     phone:"",
     email:"",
     coupon:"",
     address:""
    });

    const [records, setRecords] = useState([]);

    const handleInput = (e) =>{
        const name = e.target.name;
        const value = e.target.value;
        console.log(name,value);

        setuserRegistration({...userRegistration, [name] : value});

    }
    const handleSubmit = (e) => {
        e.preventDefault();

        const newRecord = {...userRegistration, id:new Date().getTime().toString()}
       
        setRecords([...records,newRecord]);
        console.log(newRecord);
        fetch('https://telangapadaatmnodeapp.herokuapp.com/addusers',{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(newRecord)

        })
        
        setuserRegistration({name:"",phone:"",email:"",coupon:"",address:""});
    }
    return(
        <>
        
        <form action="" onSubmit={handleSubmit}>
        <h1>Drop Message Here</h1>
            <div>
                <label htmlFor="name">Name</label>
                <input type="text" autoComplete="off" 
                 value={userRegistration.name}
                 onChange={handleInput}
                   name="name" id="name"/>
            </div>
            <div>
                <label htmlFor="phone">Phone no</label>
                <input type="text" autoComplete="off" 
                value={userRegistration.phone}
                onChange={handleInput}
                name="phone" id="phone"/>
            </div>
            <div>
                <label htmlFor="email">Email</label>
                <input type="text" autoComplete="off" 
                value={userRegistration.email}
                onChange={handleInput}
                name="email" id="email"/>
            </div>
            <div>
                <label htmlFor="coupon">Coupon no</label>
                <input type="text" autoComplete="off"
                value={userRegistration.coupon}
                onChange={handleInput} 
                name="coupon" id="coupon"/>
            </div>
            <div>
                <label htmlFor="address">Address</label>
                <input type="text" autoComplete="off"
                value={userRegistration.address}
                onChange={handleInput} 
                name="address" id="address"/>
            </div>
            <button type="submit" id="submit">Submit</button>
        </form>
        <div>
            {
                records.map((curElem)=>{
                    return(
                        
                     alert( 'Hi There!!Your message sent successfully')

                    
                        
                    )
                })
            }
        </div>
            
        </>
    )
}
export default ContactUs;